import React, { Component, createContext } from "react";
import Fetch from '../common/Fetch';


export const MainContext = createContext();

export default class TopContext extends Component {
  state = {
    headerData:{},
    cart:{},
    isShowLoginPopup:false
  };

  componentDidMount(){
    Fetch('admin/searchFilter',{from:'header'}).then(d =>{
        // console.log(d);
        
        if(d){
            let dd={...d.data};
            // delete dd['Language']
            // delete dd['Binding']
            this.setState({headerData:dd})
        }
    })
  }

  addItemToCart = item =>{
    const cartCopy={...this.state.cart};
    cartCopy[item]=item
    this.setState({cart:cartCopy})
  }

  removeFromCart = item => {
    const cartCopy={...this.state.cart};
    delete cartCopy[item]
    this.setState({cart:cartCopy})
  }

  toggleLoginPopup = bool =>this.setState({isShowLoginPopup:bool});


  render() {

    return (
      <MainContext.Provider
        value={{
          ...this.state,
          addItemToCart:this.addItemToCart,
          removeFromCart:this.removeFromCart,
          toggleLoginPopup:this.toggleLoginPopup
        }}
      >
        {this.props.children}
      </MainContext.Provider>
    );
  }
}

export const withContext = Components => props => {
  return (
    <MainContext.Consumer>
      {value => <Components context={value} {...props} />}
    </MainContext.Consumer>
  );
};
