import React from "react";
import Link from "../common/Link";
import {withContext} from '../contexts/Context';

const BookCard = function({proId, name, author, price, cashback, img,style,to ,context}) {

  const {addItemToCart,removeFromCart,cart}=context
  // console.log(context);
  
  
  return (
    <div className="book-col" style={style}>
      <div className="book__image">
        <Link to={to}>
          <img src={img} alt="Book" />
        </Link>
      </div>
      <div className="book_info">
        <div className="book-category-detail">
          <div className="book-name f-15">
            {name}
            <a href="">
              <i class="sprite-img bookmark-icon"></i>
            </a>
          </div>
          <div className="book-writer text-light f-14">{author}</div>
          <div className="book-price">
            <span className="f-15 mR5">Rs. {price}</span>
            <span className="text-red f-13">({cashback}% Cashback)</span>
          </div>
        </div>
        <div className="book-category-hoverBox">
          <div className="btn-group">
            <a  className="btn btn-border btn-radius mR5">
              BOOKMARK
            </a>
            <a onClick={ cart[proId] ? ()=> removeFromCart(proId): ()=>addItemToCart(proId)} className="btn btn-gradient btn-radius">
              {cart[proId] ? "REMOVE" : "ADD TO CART"}
            </a>
          </div>
          <div className="availability">
            <span className="f-14">Availability:</span>
            <span className="f-16 f-sm-15">In Stock</span>
          </div>
          <div className="book-price">
            <strong className="f-16">Rs. {price}</strong>
          </div>
        </div>
      </div>
    </div>
  );
};

export default withContext(BookCard);
